/* <--------------------------- * SECTION1 * --------------------------- */
.autoSaveSettingModal_container {
    position: relative;
    display: flex;
    flex-direction: column;
    padding-left: 3.2rem;
    width: 60rem;
    height: 27.2rem;
    background: $background;
    border-radius: 0.4rem;
}
.autoSaveSettingModal_title {
    padding-top: 3.2rem;
    &_title {
        display: inline-block;
        height: 3.2rem;
        color: $neutral_700;
    }
}
.autoSaveSettingModal_info {
    display: block;
    margin-top: 0.8rem;
    color: $neutral_300;
}
.autoSaveSettingModal_countContainer {
    display: flex;
    align-items: center;
    margin-top: 3.2rem;
    margin-bottom: 0.6rem;
    color: $neutral_600;
    user-select: none;
    svg {
        color: $primary_600;
    }
    .autoSaveSettingModal_count__disable {
        color: $line_light;
    }
}
.autoSaveSettingModal_info_left {
    margin-right: 1.2rem;
    color: $neutral_600;
}
.autoSaveSettingModal_info_min {
    margin-left: 1.2rem;
    margin-right: 1.5rem;
    color: $neutral_600;
}
.autoSaveSettingModal_count {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 8.1rem;
    height: 3.1rem;
    margin-right: 0.6rem;
    padding: 0 1rem;
    border: 0.1rem solid $not_activated;
    border-radius: 0.5rem;
    input {
        flex: 1;
        width: 100%;
        outline: none;
        color: $gray_2;
        text-align: center;
        display: inline-block;
    }
    span {
        display: flex;
        align-items: center;
        height: 100%;
        cursor: pointer;
    }
}
.autoSaveSettingModal_countInfo {
    @include c_body_3();
    color: $neutral_300;
}
.autoSaveSettingModal_buttons {
    position: absolute;
    bottom: 2.4rem;
    right: 2.4rem;
    button {
        font-size: 14px;
        &:nth-of-type(1) {
            color: $primary_600 !important;
        }
        &:nth-of-type(2) {
            background: $primary_600;
            box-shadow: 0px 2px 6px rgba(26, 26, 26, 0.06), 0px 3px 15px rgba(26, 26, 26, 0.1);
            border-radius: 0.6rem;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
