/* <--------------------------- * SECTION1 * --------------------------- */
.transitionReusableModal_container {
    position: relative;
    padding: 3.2rem 2.4rem;
    width: 41.4rem;
    height: 21.6rem;
    box-sizing: border-box;
}
.transitionReusableModal_title {
    display: flex;
    > span {
        @include h_4();
        color: $neutral_600;
    }
    svg {
        margin-right: 1.2rem;
        font-size: 2.4rem;
        color: $auxiliary_600;
    }
}
.transitionReusableModal_hint {
    @include body_1();
    margin-top: 1.2rem;
    color: $neutral_300;
}
.transitionReusableModal_btn {
    position: absolute;
    bottom: 1.6rem;
    right: 2.4rem;
    button {
        &:nth-of-type(1) {
            color: $primary_600 !important;
        }
        &:nth-of-type(2) {
            background: $primary_600;
            box-shadow: 0px 2px 6px rgba(26, 26, 26, 0.06), 0px 3px 15px rgba(26, 26, 26, 0.1);
            border-radius: 0.6rem;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
