/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@import '~/Constant/base.scss';
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/** The demo mixin is ..........*/
@mixin text {
    height: 1.8rem;
    line-height: 1.8rem;
    font: $fs_r_14;
    color: $gray_1;
}

/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.publishCurrent_container {
    position: relative;
    padding: 0 2.4rem;
    width: 60rem;
    height: 43.2rem;
    padding-top: 2.6rem;
    box-sizing: border-box;
    user-select: none;
}
.publishCurrent_exit {
    position: absolute;
    top: 1.6rem;
    right: 1.6rem;
    cursor: pointer;
}

.publishCurrent_header {
    display: flex;
    flex-direction: column;
    h2 {
        position: relative;
        margin-bottom: 0.8rem;
        font: $fs_r_24;
        line-height: 3.2rem;
    }
    span {
        font: $fs_r_14;
        color: $gray_3;
    }
}

.publishCurrent_info {
    margin-top: 3.2rem;
    > div {
        margin-bottom: 1.5rem;
    }
}
.publishCurrent_version {
    @include text();
    span:last-child {
        display: inline-block;
        height: 1.8rem;
        margin-left: 2rem;
        padding: 0 1rem;
        font: $fs_r_12;
        color: $gray_4;
        text-align: center;
        line-height: 1.8rem;
        border: 1px solid $gray_4;
        border-radius: 1.4rem;
    }
}
.publishCurrent_time {
    @include text();
    span:last-child {
        display: inline-block;
        height: 1.8rem;
        margin-left: 1rem;
        font: $line_dark;
        color: $gray_4;
        text-align: center;
        line-height: 1.8rem;
    }
}

.publishCurrent_description {
    > div:first-child {
        @include text();
        margin-bottom: 1.5rem;
        span {
            color: $error;
        }
    }
    .publishCurrent_errorMsg {
        font: $fs_r_12;
        color: $error;
    }
    .textArea_container {
        border-color: $neutral_200;
        &:hover,
        &:focus {
            border-color: rgba($primary_variant, 0.5);
        }
    }
}
.publishCurrent_btns {
    margin-top: 3.8rem;
    width: 100%;
    text-align: right;
    button {
        font-size: 14px;
    }
    .button_button__primary {
        background: $primary_600;
    }
    .button_secondaryButton__big {
        margin-right: 2.4rem;
        color: $primary_600 !important;
        &:hover {
            color: $primary_600 !important;
            border-color: transparent !important;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
