/* <--------------------------- * SECTION1 * --------------------------- */
.toolTip_tip {
    .kite_body {
        padding: 0.7rem 1rem;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        width: auto;
        font-size: 1.2rem;
        user-select: none;
        height: auto;
        > span {
            line-height: 1.6rem;
            user-select: none;
        }
        svg {
            margin-right: 0.4rem;
            font-size: 1.6rem;
            user-select: none;
        }
    }
}
.toolTip_scroll {
    .scroll_scrollBar__vertical {
        width: 0.2rem;
        background-color: $neutral_400;
    }
}
.toolTip_scrollBody {
    word-break: break-all;
    overflow-wrap: break-word;
}
.toolTip_none {
    position: fixed;
    padding: 0rem 0.1rem;
    font: $fs_r_12;
    box-sizing: border-box;
    top: -10000;
    left: -10000;
    white-space: nowrap;
    opacity: 0;
}
/* <--------------------------- * SECTION1 * --------------------------- */
