/* <--------------------------- * SECTION1 * --------------------------- */
.addReusableModal_container {
    display: flex;
    flex-direction: column;
    padding: 0 2.4rem;
    width: 41.4rem;
    height: 24rem;
    user-select: none;
}

.addReusableModal_title {
    @include h_2();
    color: $type;
    margin: 3.2rem 0;
    height: 2.2rem;
    width: 100%;
}

.addReusableModal_input {
    position: relative;
    color: $type;
    > span {
        display: block;
        @include subtitle_2();
        color: $neutral_600;
    }
    > div {
        position: absolute;
        color: $error;
        font: $fs_r_10;
    }
}

.addReusableModal_buttons {
    text-align: right;
    margin-top: 5.4rem;
    button {
        font-size: 14px !important;
    }
    .button_button__primary {
        background: $primary_600;
    }
    .button_secondaryButton__big {
        margin-right: 2.4rem;
        color: $primary_600 !important;
        &:hover {
            color: $primary_600 !important;
            border-color: transparent !important;
        }
    }
}

/* <--------------------------- * SECTION1 * --------------------------- */
