/* <--------------------------- * SECTION1 * --------------------------- */
.menuEdit_content {
    transform: translateX(2rem);
}
.menuEdit_edit {
    > svg {
        margin-right: 1rem;
    }
}
.menuEdit_editSecondItem {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.menuEdit_editSecondContainer_default {
    padding: 0 0.8rem;
    margin-left: 1rem;
    height: 1.9rem;
    line-height: 1.9rem;
    color: $auxiliary_600;
    background: $background;
    box-shadow: $shadow_small;
    border-radius: 0.4rem;
    &:hover {
        background: $auxiliary_100;
    }
}
.menuEdit_editSecondContainer_select {
    @include label_2();
    padding: 0 0.8rem;
    height: 1.5rem;
    line-height: 1.5rem;
    color: $auxiliary_600;
    border: 0.1rem solid $auxiliary_400;
    border-radius: 1.3rem;
}
.menuEdit_editSecondItem__selected {
    color: rgba($color: $primary_600, $alpha: 1);
}
/* <--------------------------- * SECTION1 * --------------------------- */
