.menuFile_container {
    color: $neutral_400;
}
.menuFile_file_item {
    display: flex;
    align-items: center;
    font: $fs_r_14;
    border-radius: 6px;
    cursor: pointer;
    > span {
        display: flex;
        align-items: center;
        &:nth-of-type(1) {
            margin-right: 1rem;
        }
        &:nth-of-type(2) {
            @include body_2();
        }
    }
    svg {
        font-size: 1.2rem;
    }
}
.menuFile_content {
    .caseCaderItem_container {
        &:hover {
            color: $primary_600;
        }
    }
}
