/* <--------------------------- * SECTION1 * --------------------------- */
.addNodeModal_container {
    position: relative;
    padding: 2.4rem 0;
    width: 60rem;
    height: 53.2rem;
    box-sizing: border-box;
}
.addNodeModal_title {
    @include h_2();
    margin-left: 2.4rem;
    color: $neutral_700;
}
.addNodeModal_nodeName {
    display: flex;
    flex-direction: column;
    margin-top: 3.2rem;
    padding: 0 2.4rem;
    > span {
        @include h_4();
        color: $neutral_600;
    }
    > input {
        margin-top: 0.8rem;
        border-color: $neutral_200;
    }
}
.addNodeModal_secondaryTitle {
    @include h_4();
    margin: 2.4rem;
    margin-bottom: 0.8rem;
    color: $neutral_600;
}
.addNodeModal_list {
    width: 55.2rem;
    height: 22.2rem;
    margin-left: 2.4rem;
    border: 0.6px solid $neutral_200;
    border-radius: 0.4rem;
    overflow-y: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
        width: 0 !important;
    }
}
.addNodeModal_errorMsg {
    margin-left: 2.4rem;
    margin-top: 0.5rem;
    height: 1.6rem;
    font: $fs_r_14;
    color: $danger_700;
}
.addNodeModal_item {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 4.5rem;
    padding-left: 1rem;
    font-size: 0;
    cursor: pointer;
    > span {
        @include body_2();
        color: $neutral_500;
    }
    &_arrow {
        display: flex;
        align-items: center;
        margin-right: 1.2rem;
        width: 1rem;
        > svg {
            font-size: 1rem;
        }
    }
    &_nodeIcon {
        margin-right: 1rem;
        width: 1.5rem;
        > svg {
            font-size: 1.6rem;
            color: $primary_600;
        }
    }
    &_nodeContent {
        margin-right: 1rem;
    }
    &__select {
        background: $primary_10 !important;
    }
    &:hover {
        background: $primary_10;
    }
    &_tip {
        svg {
            font-size: 1.4rem;
        }
    }
}
.addNodeModal_right {
    position: absolute;
    right: 3.3rem;
    font-size: 1.2rem;
    color: $primary_600;
}
.addNodeModal_item_child {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 3.2rem;
    padding-left: 5rem;
    font-size: 0;
    cursor: pointer;
    &__select {
        background: $primary_10 !important;
    }
    &:hover {
        background: $primary_10;
    }
    > span {
        @include body_2();
        color: $neutral_500;
    }
}
.addNodeModal_btn {
    position: absolute;
    bottom: 2.4rem;
    right: 2.4rem;
    button {
        font-size: 14px;
        &:nth-of-type(1) {
            color: $primary_600 !important;
        }
        &:nth-of-type(2) {
            background: $primary_600;
            box-shadow: 0px 2px 6px rgba(26, 26, 26, 0.06), 0px 3px 15px rgba(26, 26, 26, 0.1);
            border-radius: 0.6rem;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
