.scriptPropertyModal_ScriptWindowContainer {
    padding: 0 2.4rem;
    padding-top: 3.5rem;
    width: 41.4rem;
    padding-bottom: 1rem;
    user-select: none;
    box-sizing: border-box;
}

.scriptPropertyModal_ScriptWindowTitle {
    font-size: 3rem;
    display: flex;
    align-items: center;
    width: 31rem;
    color: $primary_600;
    > span {
        color: $primary;
        font: $fs_r_16;
        margin-left: 1rem;
    }
    svg {
        font-size: 2rem;
    }
}

.scriptPropertyModal_ScriptWindowCheckBox {
    margin-top: 1.4rem;
    display: flex;
    align-items: center;
    margin-bottom: 1.2rem;
    > span {
        color: $line_light;
        font-size: 1rem;
        margin-left: 0.4rem;
        cursor: pointer;
    }
}

.scriptPropertyModal_ScriptWindowInput {
    width: 100%;
    margin-bottom: 1.5rem;
    input {
        border: none;
        outline: none;
    }
    svg {
        font-size: 1.4rem;
    }
    > div:first-child {
        display: inline-flex;
        text-align: left;
        background: #f4f5f4;
        align-items: center;
        border: 0.1rem solid $line_light;
        box-sizing: border-box;
        border-radius: 0.6rem;
        width: 34rem;
        height: 3.1rem;
        position: relative;
        > div:first-child {
            cursor: pointer;
            display: inline-flex;
            height: calc(100% - 0.8rem);
            align-items: center;
            cursor: pointer;
            margin-left: 0.7rem;
            color: $primary_600;
            border-right: 0.1rem solid $line_light;
            white-space: nowrap;
            > span {
                color: $line_light;
                padding: 0 0.8rem;
            }
            > p {
                padding: 0;
                margin: 0;
                display: inline;
            }
        }
        > input {
            width: 100%;
            background: none;
        }
        > div:last-child {
            position: absolute;
            right: -3rem;
            top: 50%;
            transform: translateY(-50%);
            color: $primary_600;
            cursor: pointer;
        }
    }
}

.scriptPropertyModal_input__hint {
    color: $line_light;
}

.scriptPropertyModal_input__error {
    color: $error;
}

.scriptPropertyModal_ScriptWindowButtonWrapper {
    margin-top: 2.5rem;
    text-align: right;
    margin-bottom: 2.5rem;
    button {
        font-size: 14px;
    }
    > span:first-child {
        margin-right: 2.8rem;
    }
    .button_button__primary {
        background: $primary_600;
    }
    .button_secondaryButton__big {
        color: $primary_600 !important;
        &:hover {
            color: $primary_600 !important;
            border-color: transparent !important;
        }
    }
}

.scriptPropertyModal_ScriptWindowInputList {
    display: none;
    position: absolute;
    top: 3.5rem;
    z-index: 2;
}

.scriptPropertyModal_ScriptWindowAdd {
    font-size: 1.6rem;
    color: $primary_600;
    cursor: pointer;
    > span {
        margin-left: 0.7rem;
        font: $fs_r_14;
    }
}

