/* <--------------------------- * SECTION1 * --------------------------- */
.editCodeModal_container {
    position: relative;
    padding: 3.2rem 2.4rem;
    width: 60rem;
    height: 40rem;
    box-sizing: border-box;
}
.editCodeModal_title {
    @include h_2();
    color: $neutral_700;
}
.editCodeModal_hint {
    @include body_1();
    margin-top: 1.2rem;
    color: $neutral_300;
}
.editCodeModal_scroll {
    margin-top: 2.4rem;
}
.editCodeModal_options {
    width: 55.2rem;
    border: 0.1rem solid $neutral_300;
    border-radius: 0.4rem;
}
.editCodeModal_optionItem {
    display: flex;
    height: 3.6rem;
    margin-bottom: -0.1rem;
    border-bottom: 0.1rem solid $neutral_300;
}
.editCodeModal_code {
    width: 5.83rem;
    height: 3.6rem;
    line-height: 3.6rem;
    text-align: center;
    border-right: 0.1rem solid $neutral_300;
    &__repeat {
        color: red;
    }
}
.editCodeModal_content {
    width: 100%;
    height: 3.6rem;
    padding-left: 1.57rem;
    line-height: 3.6rem;
}
.editCodeModal_btn {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 6.4rem;
    padding-right: 2.4rem;
    text-align: right;
    line-height: 6.4rem;
    border-top: 0.1rem solid $neutral_200;
    box-sizing: border-box;
    button {
        &:nth-of-type(1) {
            color: $primary_600 !important;
        }
        &:nth-of-type(2) {
            background: $primary_600;
            box-shadow: 0px 2px 6px rgba(26, 26, 26, 0.06), 0px 3px 15px rgba(26, 26, 26, 0.1);
            border-radius: 0.6rem;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
