/* <--------------------------- * SECTION1 * --------------------------- */

.surveyConfigModal_container {
    user-select: none;
    width: 60rem;
    height: 51rem;
    padding: 0 2.4rem;
    padding-top: 3.2rem;
    box-sizing: border-box;
    > span {
        @include h_2();
        display: block;
        margin-bottom: 2rem;
        font: $fs_r_34;
        color: $gray_1;
        text-align: left;
        height: 2.4rem;
        line-height: 3.8rem;
    }
}

.surveyConfigModal_whole_text {
    font: $fs_r_24 !important;
}

.surveyConfigModal_exit {
    text-align: right;
    padding-right: 1.6rem;
    font: 1.2rem;
    cursor: pointer;
}

.surveyConfigModal_previewContainer {
    user-select: text;
    height: 33rem;
    width: 100%;
    padding: 0.5rem;
    font-size: 14px;
    line-height: 1.5715;
    color: rgba(0, 0, 0, 0.85);
    background: $bg;
    border: 0.03rem solid $neutral_200;
    border-radius: 0.4rem;
    word-wrap: break-word;
    box-sizing: border-box;
}

.surveyConfigModal_buttonContainer {
    width: 100%;
    margin-top: 4.8rem;
    text-align: right;
    button {
        font-size: 14px !important;
    }
    .button_button__primary {
        background: $primary_600;
    }
    .button_secondaryButton__big {
        margin-right: 2.4rem;
        color: $primary_600 !important;
        &:hover {
            color: $primary_600 !important;
            border-color: transparent !important;
        }
    }
}

/* <--------------------------- * SECTION1 * --------------------------- */
