/* <--------------------------- * SECTION1 * --------------------------- */
.addCustomScriptModal_container {
    position: relative;
    padding: 3.2rem 2.4rem;
    width: 60rem;
    height: 29.6rem;
    box-sizing: border-box;
}
.addCustomScriptModal_title {
    @include h_2();
    color: $neutral_700;
}
.addCustomScriptModal_desc {
    @include body_1();
    margin-top: 0.8rem;
    color: $neutral_300;
}
.addCustomScriptModal_scriptName {
    @include subtitle_2();
    margin-top: 3.2rem;
    margin-bottom: 0.8rem;
    color: $neutral_600;
    .generalInput_inputContainer {
        margin-top: 0.8rem;
    }
    > div {
        margin-top: 0.8rem;
    }
}
.addCustomScriptModal_btn {
    position: absolute;
    bottom: 2.4rem;
    right: 2.4rem;
    button {
        font-size: 14px !important;
        &:nth-of-type(1) {
            color: $primary_600 !important;
        }
        &:nth-of-type(2) {
            background: $primary_600;
            box-shadow: 0px 2px 6px rgba(26, 26, 26, 0.06), 0px 3px 15px rgba(26, 26, 26, 0.1);
            border-radius: 0.6rem;
        }
    }
}
.addCustomScriptModal_errorMsg {
    font-style: normal;
    font-weight: normal;
    font-size: 1.2rem;
    color: $danger_600;
}
/* <--------------------------- * SECTION1 * --------------------------- */
